@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&family=Montserrat:wght@400;700&display=swap");
@import './base.css';
@import './experience.css';
@import './layout.css';
@import './components.css';
@import './hamburger.css';
@import './projects.css';
@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&family=Montserrat:wght@400;700&display=swap");
.home-section {
  height: 100vh;
}
.navbar-list {
  height: 4vh;
  background-color: black;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: flex-end;
  font-family: "Montserrat", sans-serif;
}

.navbar-link {
  color: white;
  cursor: pointer;
  font-size: 24px;
  margin-left: 10px;
  margin-right: 10px;
  padding-right: 10px;
}

.navbar-link:hover {
  color: #008080;
}

#tsparticles {
  height: 96vh;
  left: 0;
  right: 0;
  margin: 0;
  padding: 0;
  z-index: -1;
}

.title {
  position: absolute;
  font-family: "Montserrat", sans-serif;
  top: 30%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
  color: white;
  font-size: 50px;
  text-align: center;
}

.position {
  position: absolute;
  font-family: "Montserrat", sans-serif;
  top: 70%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
  color: white;
  font-size: 30px;
  text-align: center;
}

.button-container {
  position: absolute;
  top: 80%;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1;
  text-align: center;
}

.button-container a {
  display: inline-block;
  margin: 20px 120px; /* Adjust the margin values to increase or decrease spacing */
  padding: 12px 24px;
  background-color: #333;
  color: white;
  font-family: "Montserrat", sans-serif;
  font-size: 18px;
  text-decoration: none;
  border-radius: 5px;
}

.button-container a:hover {
  background-color: #555;
  color: #008080;
}

.about-section {
  flex-grow: 1;
  margin-top: 0;
  background-color: black;
  display: flex;
  border: 2px solid black;
}

.about {
  display: flex;
  justify-content: center;
  background-color: black;
}

.profile-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-left: 250px;
}

.profile-image {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  object-fit: cover;
}

.hello-text {
  font-size: 50px;
  font-weight: bold;
  color: #008080;
  text-shadow: 0 4px 10px rgba(0, 128, 128, 0.6);
  margin-top: 10px; /* Adjust the spacing as needed */
  font-family: "Montserrat", sans-serif;
}

.additional-info {
  display: flex;
  align-items: center;
  margin-left: 300px;
  margin-right: 100px;
  color: #008080;
  font-size: 18px;
  margin-top: -100px;
  font-family: "Montserrat", sans-serif;
}

.image-container {
  display: flex;
  justify-content: center;
  gap: 100px;
}


.section-heading {
  font-size: 30px;
  font-weight: bold;
  color: black;
  font-family: "Montserrat", sans-serif;
  margin-bottom: 50px;
}


.institution {
  font-size: 20px;
  font-weight: bold;
  text-align: left;
}

.institution-date-container {
  display: flex;
  justify-content: space-between;
}

.date {
  font-size: 16px;
  color: #888;
  text-align: right;
}

.degree {
  font-size: 18px;
  margin-bottom: 5px;
  text-align: left;
}

.date {
  font-size: 16px;
  color: #888;
}

.relevant-coursework {
  text-align: left;
}

.coursework-heading {
  font-size: 18px;
  font-weight: bold;
}

.coursework-list {
  list-style-type: disc;
  margin-left: 10px;
}

.coursework-list li {
  margin-bottom: 5px;
}

.code-link {
  display: inline-block;
  color: black;
  text-decoration: none;
  border-radius: 4px;
  font-weight: bold;
}

.code-link:hover {
  text-decoration: underline;
}

.skills-section {
  /* Add your styles for the skills section container */
  text-align: center;
  border: 2px solid white;
}

.skills-title {
  /* Add your styles for the skills title */
  font-size: 30px;
  font-weight: bold;
  color: black;
  font-family: "Montserrat", sans-serif;
}

.skills-grid {
  display: grid;
  grid-template-columns: repeat(6, 1fr); /* Display 6 skills per row */
  grid-gap: 20px;
  margin-left: auto;
  margin-right: auto;
  max-width: 75%; /* Adjust the percentage as desired */
}

.skill-icon {
  width: 100px; /* Make the image fill the entire container */
  height: 100px;
  object-fit: cover;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3); /* Adjust the box shadow properties */
  border-radius: 10px; /* Add rounded edges */
}

.image-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-right: 50px;
}

.image-container {
  margin-right: 0;
  margin-bottom: 30px;
}